import React from "react";
import { Jumbotron, Button } from "reactstrap";
import Navigation from "./Navigation";
import BGI from "../Images/homeOffice.jpg";
import Logo from "../Images/Logo.png";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";

const Hero = (props) => {
	return (
		<Jumbotron
			style={{
				height: "100vh",
				backgroundImage: `url(${BGI})`,
				backgroundSize: "cover",
				backgroundPosition: "top",
			}}
			className="p-0 text-white rounded-0 bg-danger text-center"
		>
			<div className="h-100 hero-content">
				<Navigation isHomeNav={true} />
				<div className="d-flex align-items-center justify-content-center h-100">
					<div>
						<img src={Logo} alt="SBWL DATA" style={{ width: "40%" }} />
						<h1 className="display-2 font-weight-bold heading">SBWL Data</h1>
						<hr className="my-2 style-one" color="white" />
						<p className="h2 mb-4 subText">The right Mobile network for you.</p>
						{/* <p>
							It uses utility classes for typography and spacing to space
							content out within the larger container.
						</p> */}
						<p className="lead">
							<HashLink to="/#deals">
								<Button color="light" className="rounded font-weight-bold">
									Get Connected
								</Button>
							</HashLink>
						</p>
					</div>
				</div>
			</div>
		</Jumbotron>
	);
};

export default Hero;
