import React, { useState } from "react";
// import { Redirect } from "react-router-dom";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	DropdownItem,
	Form,
	FormGroup,
	Label,
	Input,
	Alert,
} from "reactstrap";
// import axios from "axios";

export default function Login(props) {
	// set initail states

	let [username, setUsername] = useState("");
	let [password, setPassword] = useState("");
	// let [errorMessage, setErrorMessage] = useState("");
	// const [redirect, setRedirect] = useState(false);
	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	function handleUsernameChange(e) {
		// on change function for title
		setUsername(e.target.value);
	}

	function handlePasswordChange(e) {
		// on change function for description
		setPassword(e.target.value);
	}

	function handleSubmit() {
		// stops browser from refreshing
		// cast username and password into an object
		// const data = {
		// 	username: username,
		// 	password: password,
		// };
		// props.handleLogin(data);
		// clear();
	}

	function clear() {
		setPassword("");
		setUsername("");
	}

	// if (redirect) {
	// 	return <Redirect to="/Shops" />;
	// }

	return (
		<React.Fragment>
			<DropdownItem onClick={toggle}>Sign In</DropdownItem>
			<Modal isOpen={modal} toggle={toggle}>
				<Form onSubmit={handleSubmit} className="text-center">
					<ModalHeader toggle={toggle} className="border-0 text-center">
						<p>Please Login</p>
					</ModalHeader>
					<ModalBody>
						<FormGroup>
							{/* <Label className="text-muted">Username</Label> */}
							<Input
								name="username"
								type="text"
								value={username}
								onChange={handleUsernameChange}
								placeholder="Username"
								autofocus
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
							/>
						</FormGroup>
						<FormGroup>
							{/* <Label className="text-muted">Password</Label> */}
							<Input
								name="password"
								type="password"
								value={password}
								onChange={handlePasswordChange}
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Password"
							/>
						</FormGroup>
						{/* <p className="mt-1 text-danger">{errorMessage}</p> */}
						{/* <Alert color="danger">
							{errorMessage}
							<Button className="close" data-dismiss="alert" aria-label="Close">
								<span aria-hidden="true">&times;</span>
							</Button>
						</Alert> */}
					</ModalBody>
					<ModalFooter className="border-0">
						<Button
							type="button"
							onClick={handleSubmit}
							color="primary"
							outline
							block
						>
							Login
						</Button>
						<Button color="secondary" onClick={toggle} block>
							Cancel
						</Button>
					</ModalFooter>
				</Form>
			</Modal>
		</React.Fragment>
	);
}
