import React, { useState } from "react";
import Navigation from "../Components/Navigation";
import { Form, Label, Input, FormGroup, Button } from "reactstrap";
import Col from "reactstrap/lib/Col";
// import { useState } from "react";
import PersonalInfo from "../Components/SignUpComponents/PersonalInfo";
import DeliveryInfo from "../Components/SignUpComponents/DeliveryInfo";
import Summary from "../Components/SignUpComponents/Summary";

function Signup() {
	const intialPersonalInfo = {
		name: "",
		surname: "",
		username: "",
		password: "",
	};

	const intialDeliveryInfo = {
		street: "",
		suburb: "",
		town: "",
		postCode: null,
	};
	const [step, setStep] = useState(1);
	let [personalInfo, setPersonalInfo] = useState(intialPersonalInfo);
	let [deliveryInfo, setDeliveryInfo] = useState(intialDeliveryInfo);

	function handlePersonalChange(e) {
		let updatePersonalInfo = personalInfo;
		updatePersonalInfo[e.target.name] = e.target.value;
		setPersonalInfo(updatePersonalInfo);
	}

	function handleDeliveryChange(e) {
		let updateDeliveryInfo = deliveryInfo;
		updateDeliveryInfo[e.target.name] = e.target.value;
		setDeliveryInfo(updateDeliveryInfo);
	}

	function next() {
		setStep(step + 1);
	}

	function back() {
		setStep(step - 1);
	}

	function steps() {
		if (step === 1) {
			return (
				<PersonalInfo
					personalInfo={personalInfo}
					handlePersonalChange={handlePersonalChange}
					next={next}
				/>
			);
		} else if (step === 2) {
			return (
				<DeliveryInfo
					deliveryInfo={deliveryInfo}
					handleDeliveryChange={handleDeliveryChange}
					next={next}
					back={back}
				/>
			);
		} else if (step === 3) {
			return (
				<Summary
					personalInfo={personalInfo}
					deliveryInfo={deliveryInfo}
					back={back}
				/>
			);
		}
	}
	return (
		<React.Fragment>
			<Navigation isHomeNav={false} />
			<Form className="p-5">
				<p className="h2 text-center"> Step {step} of 3</p>
				<div className="row my-5">{steps()}</div>
			</Form>
		</React.Fragment>
	);
}

export default Signup;
