import React from "react";
import { Form, Label, Input, FormGroup, Button } from "reactstrap";

function Summary({ back, personalInfo, deliveryInfo }) {
	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-4 offset-lg-4 border px-5 pb-5">
				<legend>Summary Information</legend>
				<p>Name: {personalInfo.name}</p>
				<p>Surname: {personalInfo.surname}</p>
				<p>Username: {personalInfo.username}</p>
				<p>
					Address: {deliveryInfo.street}, {deliveryInfo.suburb},{" "}
					{deliveryInfo.town}, {deliveryInfo.postCode}
				</p>
				<Button color="success" block>
					Place Order
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
		</React.Fragment>
	);
}

export default Summary;
