import React from "react";
import { CardImg } from "reactstrap";
import Wifi from "./../Images/wifi.png";

function VisionMission() {
	return (
		<React.Fragment>
			<div className="row my-5 p-5 justify-content-around">
				<div className=" col-12 col-lg-6  py-3">
					<h1 className="font-weight-bold text-center my-4 ml-5">About</h1>
					<hr className="w-50 style-two" />
					<blockquote className="blockquote mb-0 text-justified">
						<p>
							Lorem ipsum dolor sit amet consectetur adipisicing elit. Dicta
							quasi ea ab ipsam. Vitae, atque dolorum itaque laborum, ad
							repellendus voluptatum eligendi fugit eos impedit doloremque
							consequatur tempore et voluptatem.
						</p>
						<footer className="blockquote-footer text-dark-50">
							John Doe <cite title="Source Title">CEO</cite>
						</footer>
					</blockquote>
				</div>
				<div className="col-12 col-lg-3 text-center d-flex justify-content-center">
					<CardImg width="50px" src={Wifi} alt="Card image cap" />
				</div>
			</div>
		</React.Fragment>
	);
}

export default VisionMission;
